@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400&display=swap");@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");:root{--bg1: #347bf7;--bg2: #e9e9e9;--bg3: #ffffff;--bg4: #ffffff;--bg5: #ffbc00;--col1: #fff;--col2: #000;--col3: #f00179;--col4: #171544;--col5: #171544}body{font-family:"Lato", sans-serif;font-weight:400}button,input{outline:none !important}*{-webkit-box-sizing:border-box;box-sizing:border-box}::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#f5f5f5;border-radius:10px}::-webkit-scrollbar{width:10px;background-color:#f5f5f5}::-webkit-scrollbar-thumb{border-radius:10px;background-image:-webkit-gradient(linear, left bottom, left top, color-stop(0.33, #347bf7), color-stop(0.66, #2b67cf), color-stop(0.99, #2554a5))}
